<template>
    <!-- 推荐歌单 -->
    <router-link :to="`/playlist/${item.id}`" tag="li" class="item">
        <div>
            <img :src="item.picUrl+'?imageView=1&type=webp&thumbnail=369x0'"/>
            <span>{{item.playCount|formatNum}}万</span>
        </div>
        <p>{{item.name}}</p>
    </router-link>
</template>
<script>
export default {
    name:"ListItem",
    props:["item"],
    created(){
        // console.log(this.item);
    },
    filters:{
        formatNum(val){
            return(val/10000).toFixed(1);
        }
    }
}
</script>

<style lang="less" scoped>
li.item{
    display: flex;
    flex-direction: column;
    div{
        position: relative;
        span{
            position: absolute;
            right: 4px;
            top: 4px;
            font-size: 8px;
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-size: 10px;
            line-height: 10px;
            color:white;
            text-shadow: 0px 0px 2px #000;
            padding-left: 12px;
        }
    }
    &>p{
        display: inline-block;
        text-decoration: none;
        width: 100%;
        font-size: 8px;
        color:#333;
        padding:0px 0px 10px 8px;
    }
}
</style>